import React, { Component } from "react";
import Banner from "./Banner";
import { FetchFeed } from "../../actions/Feed";
import { ShowAddExpSection } from "../../actions/AddExp";
import { connect } from "react-redux";
import CustomizedDialogs from "../Modal";
import Loader from "../Loader";
import { withRouter } from "react-router";
import Feeds from "../Feeds";
import DialogBox from "../DialogBox";
import firebase from "../../firebaseConfig";
class Home extends Component {
  state = {
    showLoading: true,
    jokes: null,
  };
  componentDidMount() {
    this.props.FetchFeed(() => console.log("d"));
  }

  render() {
    const handleAddComment = (text) => {
      alert(text);
    };
    const handleAddExpButton = (text) => {
      this.props.ShowAddExpSection(text);
    };
    let error = this.props.feedInfo.failure ? (
      <CustomizedDialogs
        title="Error"
        message={this.props.feedInfo.error}
        initialState={true}
      />
    ) : (
      ""
    );
    return (
      <div className="home_Container">
        {!this.props.feedInfo.feed && <Loader showLoading={true}/>}
        <Banner />
        {error}
        {console.log("fe", this.props.feedInfo)}
        {this.props.feedInfo.feed && <Feeds jokes={this.props.feedInfo.feed} />}
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  feedInfo: state.Feed,
  showCommentBox: state.ExpReducer.showCommentBox,
});

export default withRouter(
  connect(mapStateToProps, { FetchFeed, ShowAddExpSection })(Home)
);
