import React from "react";
import { FormHelper } from "react-form-helper";

export const Register = () => {
  const submitRegistration = (credentials) => {
    alert("service not available");
  };

  return (
    <FormHelper
      saveButton="Register"
      buttonProps={{
        style: { padding: 8, background: "lightblue", border: "none" },
      }}
      onSave={(credentials) => submitRegistration(credentials)}
      fields={[
        {
          path: ["email"],
          label: "Email",
          style: { border: "2px solid lightblue" },
          divProps: { style: { padding: 8 } },
          labelSpanProps: { style: { display: "inline-block", width: 120 } },
        },
        {
          path: ["firstName"],
          label: "First Name",
          style: { border: "2px solid lightblue" },
          divProps: { style: { padding: 8 } },
          labelSpanProps: { style: { display: "inline-block", width: 120 } },
        },
        {
          path: ["lastName"],
          label: "Last Name",
          style: { border: "2px solid lightblue" },
          divProps: { style: { padding: 8 } },
          labelSpanProps: { style: { display: "inline-block", width: 120 } },
        },
        {
          path: ["password"],
          label: "Password",
          type: "password",
          style: { border: "2px solid lightblue" },
          divProps: { style: { padding: 8 } },
          labelSpanProps: { style: { display: "inline-block", width: 120 } },
        },
      ]}
    />
  );
};
