import React from "react";
import TablePagination from "@material-ui/core/TablePagination";

export default function TablePaginationDemo({
  count,
  initialCount,
  changeInitialCount,
  changeStartIndex,
}) {
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(initialCount);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    changeInitialCount(newPage * 10);
    changeStartIndex((newPage - 1) * 10);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, initialCount));
    setPage(0);
  };

  return (
    <TablePagination
      component="div"
      count={count}
      page={page}
      onChangePage={handleChangePage}
      rowsPerPage={10}
      onChangeRowsPerPage={handleChangeRowsPerPage}
    />
  );
}
