import React from "react";
import Button from "@material-ui/core/Button";
import { withStyles } from '@material-ui/core/styles';
import TextField from "@material-ui/core/TextField";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";

const loginerr = ()=>{

}

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: "absolute",
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const FormDialog = withStyles(styles)(({ initialState, handleAddComment,classes, OnClose,props }) => {
  const [open, setOpen] = React.useState(initialState);
  const [exptext, setText] = React.useState("");
  

  const handleClose = () => {
    setOpen(false);
    OnClose(false);
  };

  return (
    <div>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Add Experience</DialogTitle>
        <DialogContent>
          <TextField
            id="outlined-multiline-static"
            multiline
            rows={20}
            defaultValue="Default Value"
            variant="outlined"
            value={exptext}
            onChange={(e) => setText(e.target.value)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={() => handleAddComment(exptext)} color="primary">
            Add
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
});

export default FormDialog;
