import React from "react";
import TablePaginationDemo from "./TablePaginationDemo";

const Feeds = ({ jokes }) => {
  const html = [];
  const [initialCount, setinitialCount] = React.useState(10);
  const [startIndex, setstartIndex] = React.useState(-10);

  jokes.slice(0, 10).forEach((j) => {
    const random = Math.floor(Math.random() * jokes.length);
    const joke = jokes[random];
    html.push(<li className="list-group-item">{joke["joke"]}</li>);
  });
  return (
    <div className="container">
      <div className="card">
        <div className="card-header"></div>
        <ul className="list-group list-group-flush">{html}</ul>
      </div>
      <div>
        <TablePaginationDemo
          count={jokes && jokes.length}
          initialCount={initialCount}
          changeInitialCount={setinitialCount}
          changeStartIndex={setstartIndex}
          startIndex={startIndex}
        />
      </div>
    </div>
  );
};

export default Feeds;
