import React from "react";
import { Link, useHistory } from "react-router-dom";
import { useOktaAuth } from "@okta/okta-react";
import Loader from "../Loader";
const LoggedInView = ({ email, currentUser, onLogutClick }) => (
  <ul className="nav navbar-nav pull-xs-right" style={{ flexDirection: "row" }}>
    <li className="nav-item home_nav">
      <Link to="/react-day-experience-app" className="nav-link">
        Home
      </Link>
    </li>
    <li className="nav-item">
      <Link to="/profile" className="nav-link">
        Profile
      </Link>
    </li>
    <li className="nav-item">
      <Link onClick={onLogutClick} className="nav-link">
        Log Out
      </Link>
    </li>
  </ul>
);
const LoggedOutView = () => (
  <ul className="nav navbar-nav pull-xs-right" style={{ flexDirection: "row" }}>
    <li className="nav-item">
      <Link to="/react-day-experience-app" className="nav-link">
        Home
      </Link>
    </li>

    <li className="nav-item">
      <Link to="/login" className="nav-link">
        Sign in
      </Link>
    </li>

    <li className="nav-item">
      <Link to="/register" className="nav-link">
        Sign up
      </Link>
    </li>
  </ul>
);

const Nav = (props) => {
  const history = useHistory();
  const logout = async () => {
    authService.logout("/login");

    // history.push("/login");
  };
  const { authState, authService } = useOktaAuth();
  const [userInfo, setUserInfo] = React.useState(null);
  const [isAuthenticated, SetAuthentication] = React.useState(false);
  React.useEffect(() => {
    if (!authState.isAuthenticated) {
      // When user isn't authenticated, forget any user info
      SetAuthentication(false);
      setUserInfo(null);
    } else {
      SetAuthentication(true);
      authService.getUser().then((info) => {
        setUserInfo(info);
      });
    }
  }, [authState, authService]);
  return (
    <nav className="navbar navbar-light">
      <div className="container">
        <Link to="/react-day-experience-app" className="navbar-brand">
          Jokes For the Day
        </Link>
        {isAuthenticated && userInfo ? (
          <LoggedInView onLogutClick={logout} />
        ) : (
          <LoggedOutView />
        )}
      </div>
    </nav>
  );
};

export default Nav;
