import React, { useState, useEffect } from "react";
import { useOktaAuth } from "@okta/okta-react";
import Loader from "../Loader";

const Profile = () => {
  const { authState, authService } = useOktaAuth();
  const [userInfo, setUserInfo] = useState(null);

  useEffect(() => {
    if (!authState.isAuthenticated) {
      // When user isn't authenticated, forget any user info
      setUserInfo(null);
    } else {
      authService.getUser().then((info) => {
        setUserInfo(info);
      });
    }
  }, [authState, authService]); // Update if authState changes

  if (!userInfo) {
    return (
      <div>
        <Loader showLoading={true} />
      </div>
    );
  }

  return (
    <div className="container">
      <div className="card">
        <div className="card-header">Profile Info</div>
        <ul className="list-group list-group-flush">
          <div className="pro_Container">
            <div>
              <div className="label">User Name :</div>
              <div>{userInfo["name"]}</div>
            </div>
            <div>
              <div className="label">Email Address :</div>
              <div>{userInfo["email"]}</div>
            </div>
            <div>
              <div className="label">First Name :</div>
              <div>{userInfo["given_name"]}</div>
            </div>
            <div>
              <div className="label">Last Name :</div>
              <div>{userInfo["family_name"]}</div>
            </div>
            <div>
              <div className="label">Last Name :</div>
              <div>{userInfo["family_name"]}</div>
            </div>
            <div>
              <div className="label">Middle Name :</div>
              <div>{userInfo["middle_name"]}</div>
            </div>
            <div>
              <div className="label">preferred Name :</div>
              <div>{userInfo["preferred_username"]}</div>
            </div>
          </div>
        </ul>
      </div>
      <div></div>
    </div>
  );
};

export default Profile;
